import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

const AskPaymentModal = ({ show, proceed, cancel }) => {
  return (
    <>
      <Modal
        centered={true}
        modalTransition={{ timeout: 200 }}
        backdropTransition={{ timeout: 10 }}
        backdrop="static"
        isOpen={show}
        className={"className"}
      >
        <ModalHeader>Lanjut ke Pembayaran?</ModalHeader>
        <ModalBody>
          Jika Anda ingin melanjutkan ke menu{" "}
          <span className="title">Pembayaran</span>, maka anda{" "}
          <span className="title text-danger">tidak bisa </span>
          lagi mengubah ataupun menambah belanjaan anda <br />
          Apakah anda yakin?
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={proceed}>
            Lanjut ke Pembayaran
          </Button>
          <Button color="secondary" onClick={cancel}>
            Batal
          </Button>
        </ModalFooter>
      </Modal>
    </>
  );
};

export default AskPaymentModal;
