import React from "react";

import { Card, CardHeader, CardBody, CardTitle, Table } from "reactstrap";
import { useCart } from "../../../api/Cart";
import PurchasedItem from "./_PurchasedItem.js";

const Purchased = () => {
  const items = useCart();
  return (
    <Card style={{ maxHeight: "500px", overflowY: "scroll" }}>
      <CardBody>
        <Table hover>
          <thead className="text-primary">
            <tr>
              <th>produk</th>
              <th>nama</th>
              <th>harga</th>
              <th>Qty.</th>
              <th className="text-right">SubTotal</th>
            </tr>
          </thead>
          <tbody>
            {items.map((item, i) => {
              return <PurchasedItem key={i} item={item} />;
            }, 0)}
          </tbody>
        </Table>
      </CardBody>
    </Card>
  );
};

export default Purchased;
