import React from "react";

const ReceiptItem = (props) => {
  const { item } = props;
  return (
    <tr>
      <th scope="row">
        {item.nama}
        <p className="fs-6 fw-light text-muted">Rp {item.harga}</p>
      </th>
      <td>{item.jumlah}</td>
      <td>Rp {item.jumlah * item.harga}</td>
    </tr>
  );
};

export default ReceiptItem;
