import React, { useState, useEffect } from "react";
import { useDispatchCart } from "../../../api/Cart";
import {
  addPurchasedItem,
  removePurchasedItem,
} from "../../../api/CartOperation";
import {
  Badge,
  Button,
  Col,
  Row,
  Card,
  CardImg,
  CardBody,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
} from "reactstrap";

import "./_RemoveItemCard.css";

const RemoveItemCard = ({ purchasedItem, user }) => {
  const dispatch = useDispatchCart();

  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [count, setCount] = useState(parseInt(purchasedItem.jumlah));

  const dropdownToggle = (e) => {
    setDropdownOpen(!dropdownOpen);
  };

  const addToCart = (item) => {
    addPurchasedItem(item).then((data) => dispatch({ type: "ADD", data }));
  };

  const deleteFromCart = (item) => {
    removePurchasedItem(item).then((data) =>
      dispatch({ type: "REMOVE", data })
    );
  };

  const editItem = (item) => {
    if (count > purchasedItem.jumlah) {
      addToCart(item);
    } else if (count < purchasedItem.jumlah && count > 0) {
      deleteFromCart(item, user);
    }
    // else if (count === 0) {
    //   const item = { ...data };
    //   dispatch({ type: "DELETE", item, user });
    // }
  };

  const removeItem = (data) => {
    const item = { ...data };
    dispatch({ type: "DELETE", item, user });
  };

  const increment = () => {
    setCount((count) => count + 1);
  };
  const decrement = () => {
    count === 0 ? setCount(0) : setCount(count - 1);
  };

  return (
    <Col xs="12" sm="6" md="6" lg="6">
      <Card className="purchased-card-modal">
        <Row>
          <Col md="12" lg="3" className="d-flex justify-content-center">
            <CardImg
              src={`https://keranjang-pintar.herokuapp.com/gambar/${purchasedItem.gambar}`}
              alt="..."
              className="purchased-img-modal"
            />
          </Col>
          <Col md="12" lg="9">
            <div className="purchased-text-group-modal">
              <CardBody>
                <div className="puchased-text-top-modal">
                  <p className="h5 purchased-title-modal purchased-text-modal">
                    {purchasedItem.nama}
                  </p>
                </div>
                <Badge color="success purchased-badge-modal">
                  <p className="purchased-price-text-modal">
                    Rp {purchasedItem.harga}
                  </p>
                </Badge>
              </CardBody>
            </div>
          </Col>
          <div className="purchased-text-jumlah-modal">
            <p>x {purchasedItem.jumlah}</p>
          </div>
        </Row>
      </Card>
    </Col>
  );
};

export default RemoveItemCard;
