import React from "react";
import {
  Button,
  Col,
  Row,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import RemoveItem from "./_RemoveItem/Index";
import { useUser } from "../../api/User";
import Purchased from "./_Purchased/Index";

const Cart = ({ isDecrease, setIsDecrease }) => {
  const user = useUser();
  return (
    <div style={{ overflowY: "hidden" }}>
      <Modal
        size={"xl"}
        modalTransition={{ timeout: 200 }}
        backdropTransition={{ timeout: 10 }}
        backdrop="static"
        isOpen={isDecrease}
        contentClassName="modal-content-style"
        modalClassName="modal-style"
        wrapClassName="modal-wrap-style"
      >
        <ModalBody>
          <Row>
            <Col lg="5">
              <Purchased />
            </Col>
            <Col
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "space-between",
              }}
            >
              <div>
                <p>
                  Kami mendeteksi bahwa anda telah melakukan{" "}
                  <span className="title">Pengurangan Benda </span>pada
                  keranjang. Harap memilih{" "}
                  <span className="title text-danger">
                    Benda yang dikeluarkan{" "}
                  </span>
                  dari keranjang
                </p>
                <br /> <RemoveItem user={user} />
              </div>
              <ModalFooter style={{ justifyContent: "space-between" }}>
                <p className="">
                  Sisa
                  <span className="title text-primary"> {`0,00 gr`} </span>
                  untuk dikeluarkan
                </p>
                <Button color="primary" onClick={() => setIsDecrease(false)}>
                  Lanjut Belanja
                </Button>
              </ModalFooter>
            </Col>
          </Row>
        </ModalBody>
      </Modal>
    </div>
  );
};

export default Cart;
